.home-page{
    .section-feature{
        .item-feature{
            img{
                width: 100px;
                height: 100px;
                @include media-max(md){
                    width: 80px;
                    height: 80px;
                }
            }
        }
        .item-feature-title{
            font-size: 24px;
            line-height: 36px;
            font-weight: bold;
            color: map-get($index-group,'item-feature-title-color');
            @include media-max(md){
                font-size: 20px;
                line-height: 30px;
            }
        }
        .item-feature-content{
            font-size: 16px;
            line-height: 24px;
            max-width: 208px;
            margin: 0 auto;
            text-align: left;
            @include media-max(md){
                max-width: 237px;
            }
        } 
    }
    .section-ad{
        .item-ad-list{
            li{
                position: relative;
                display: flex;
                align-items: center;
                margin-bottom: 32px;
                @include media-max(md){
                    margin-bottom: 8px;
                }
                &:nth-child(2){
                    flex-direction: row-reverse;
                    .item-ad-image{
                        margin-left: 30px;
                        @include media-max(sm){
                            margin-left: 0px;
                        }
                    }
                    @include media-max(sm){
                        flex-direction: inherit;
                    }
                }
                .item-ad-image{
                    max-width: 635px;
                    @include media-max(md){
                        max-width: calc( 100% - 340px);
                    }
                    @include media-max(xs){
                        max-width:100%;
                    }
                }
                .item-ad-content{
                    display: flex;
                    align-items: center;
                    font-weight: bold;
                    font-size: 48px;
                    line-height: 72px;
                    max-width: 100%;
                    text-transform: uppercase;
                    color: map-get($index-group,'product-name-color-lg');
                    img{
                        max-width: 48px;
                    }
                    @include media-max(lg){
                        font-size: 40px;
                    }
                    @include media-max(md){
                        font-size: 28px;
                        line-height: 42px;
                        min-width: 340px;
                        img{
                            max-width: 32px;
                        }
                    }
                    @include media-max(xs){
                        position: absolute;
                        top:50%;
                        left:50%;
                        transform: translate(-50%,-50%);
                        display: flex;
                        justify-content: center;
                        color: map-get($index-group,'product-name-color-xs');
                    }
                }
            }
        }
    }
    .section-cooperation-product{  // 聯名設計
        background: url(map-get($index-group,'section-cooperation-product'));
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        // min-height:743px;
        .item-cooperation{
            background: white;
            color: map-get($index-group,'product-name-color-lg');
            font-size: 72px;
            line-height: 108px;
            text-align: center;
            position:relative;
            margin-bottom: 16px;
            img{
                // position:absolute;
                // top: 0;
                // left: 0;
                opacity: .5;
            }
            p{
                position:absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: 72px;
                line-height: 108px;
                font-weight: bold;
                width: 340px;
                @include media-max(md){
                    font-size: 32px;
                    line-height: 25px;
                    font-weight: bold;
                }
            }
            @include media-max(sm){
                max-width: 678px;
                margin:0 auto 16px auto;
            }
        }
    }
    .section-customer-recommend{ // 顧客推薦
        color:map-get($index-group,'card-title-color');
    }
    .section-contact{  // 聯繫
        color: map-get($index-group,'content-color') !important;
        .contact-content{
            max-width: 730px;
            @include media-max(sm){
                max-width: 450px;
            }
            p{
                font-size: 20px;
                line-height: 30px;
                @include media-max(md){
                    font-size: 16px;
                    line-height: 24px;
                }
            }
            .form-control{
                font-size: 16px;
                line-height: 24px;
                color: map-get($color-group,'content-border-color');
                border-radius: 0;
                border: 1px solid map-get($color-group,'content-border-color');
                @include media-max(md){
                    font-size: 14px;
                    line-height: 21px;
                }
            }
            .form-check{
                margin-bottom: 42px;
                @include media-max(md){
                    margin-bottom: 20px;
                }
            }
            .btn-submit{
                width: 350px;
                border-radius: 0 ;
                font-size: 16px;
                line-height: 24px;
                padding: 16px 0;
                margin-bottom: 16px;
                @include media-max(md){
                    width: 210px;
                    font-size: 14px;
                    line-height: 21px;
                    padding: 8px 0;
                    margin-bottom: 40px;
                }
                @include media-max(xs){
                    width: 165px;
                    margin-bottom: 32px;
                }
            }
        }
    }
}