.inner-page{
    .product-slider{
        display: flex;
        .item-slider{
            &.__slider-1{
                background: url(map-get($banner-group,'slider-inner-image-1'));
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center center;
            }
            &.__slider-2{
                background: url(map-get($banner-group,'slider-inner-image-2'));
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center center;
            }
            min-height: 309px;
            width: calc(100% / 2 - 15px);
            &:first-child{
                margin-right: 30px;
            }
            @include media-max(sm){
                width: calc(100% / 2);
                &:first-child{
                    margin-right: 0px;
                }
            }
            @include media-max(xs){
                min-height: 145px;
            }
        }
    }
    .sub-slider{
        background: url(map-get($banner-group,'slider-inner-location-banner'));
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        .slider{
            display: flex;
            align-items: flex-end;
            min-height: 304px;
            .content{
                margin-bottom: 8px;
            }
            @include media-max(xs){
                min-height: 143px;
            }
            img{
                width: 40px;
                height: 57px;
                @include media-max(sm){
                    width: 30px;
                    height: 40px;
                }
                @include media-max(xs){
                    font-size: 16px;
                    line-height: 24px;
                }
            }
        }
        h1{
            margin: 0;
            font-weight: bold;
            font-size: 64px;
            line-height: 80px;
            margin-right: 8px;
            @include media-max(sm){
                font-size: 48px;
                line-height: 80px;
            }
            @include media-max(xs){
                font-size: 28px;
                line-height: 80px;
                
            }
        }
    }
    ul.product-menu{
        margin: 0 auto;
        max-width: 1380px;
        display: flex;
        align-items: center;
        align-content: baseline;
        height: 103px;
        @include media-max(md){
            height: 69px;
        }
        @include media-max(xs){
            height: 35px;
        }
        li{
            width: calc(100% / 3);
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid map-get($inner-group,'sub-menu-border-color');
            a{
                text-decoration: none;
                font-size: 43px;
                line-height: 63px;
                color: black;
                display: block;
                text-align: center;
                @include media-max(md){
                    font-size: 28px;
                    line-height: 48px;
                }
                @include media-max(sm){
                    font-size: 18px;
                    line-height: 19px;
                }
                @include media-max(xs){
                    font-size: 14px;
                    line-height: 19px;
                }
                
                img{
                    height: 48px;
                    @include media-max(md){
                        height: 32px;
                    }
                    @include media-max(sm){
                        height: 28px;
                    }
                    @include media-max(xs){
                        height: 15px;
                    }
                }
            }
            &.active{
                border-bottom: 16px solid map-get($inner-group,'sub-menu-active');
                @include media-max(md){
                    border-bottom: 8px solid map-get($inner-group,'sub-menu-active');
                }
                @include media-max(xs){
                    border-bottom: 4px solid map-get($inner-group,'sub-menu-active');
                }
            }
        }
    }
    .sub-menu{
        background: #650300;
        display: flex;
        justify-content: space-between;
        ul{
            display: flex;
            @include media-max(xs){
                width: 100%;
                justify-content: space-between;
            }
        }
        li{
            padding: 10px 48px 10px 0;
            @include media-max(sm){
                padding: 16px 32px 16px 0;
            }
            @include media-max(xs){
                padding: 16px 0px 16px 0;
            }
            a{
                color: white;
                text-decoration: none;
                font-size: 20px;
                line-height: 24px;
                font-weight: bold;
                @include media-max(sm){
                    font-size: 16px;
                    line-height: 24px;
                }
            }
        }
        img{
            width: 20px;
            @include media-max(xs){
                display: none;
            }
        }
        @include media-max(xs){
            // display: none;
        }
    }
    main{
        // section-products
        .item-product-list{
            display: flex;
            flex-wrap: wrap;
            justify-content: end;
            .item-product{
                width: calc(100% / 4);
                max-width: 255px;
                margin-bottom: 104px;
                padding: 15px;
                @include media-max(md){
                    width: calc(100% / 3);
                    max-width: 330px;
                }
                @include media-max(sm){
                    width: calc(100% / 2);
                    max-width: 330px;
                }
                @include media-max(xs){
                    width: 100%;
                    max-width: 100%;
                }
                img{
                    width: 100%;
                }
            }
            .item-product-content{
                display: flex;
                justify-content: space-between;
                text-transform: uppercase;
                .item-product-name{
                    font-size: 24px;
                    line-height: 36px;
                }
                .item-product-price{
                    font-size: 24px;
                    line-height: 36px;
                    color: map-get($inner-group,'product-price-color');
                    text-align: right;
                }
            }
        }
        .section-location{
            padding: 12px 0 304px 0;
            .input-group{
                padding-bottom: 32px;
            }
            .custom-select{
                border-radius: 5px;
                width: 324px;
                color: map-get($inner-group,'input-text-color');
                border: 1px solid map-get($inner-group,'input-border-color');
                @include media-max(md){
                    width: 237px;
                }
                @include media-max(xs){
                    width: calc(100% - 88px);
                }
            }
            .location-list{
                display: flex;
                flex-wrap: wrap;
                justify-content: end;
                li.item-location{
                    width: calc(100% / 3);
                    padding: 0 15px;
                    margin-bottom: 32px;
                    @include media-max(sm){
                        width: calc(100% / 2);
                        
                    }
                    @include media-max(xs){
                        width: 100%;
                        margin: 0 auto 32px auto;
                    }
                }
            }
            .location-area{
                display: flex;
                justify-content: space-between;
                @include media-max(sm){
                    display: block;
                }
                @include media-max(xs){
                    // display: flex !important;
                }
                .item-location{
                    .__location-card{
                        max-width: 445px;
                        padding: 0;
                        margin-right: 15px;
                        @include media-max(sm){
                            margin-right: 0px;
                            margin-bottom: 30px;
                        }
                        @include media-max(xs){
                            flex-direction: column !important;
                        }
                        .card-pic{
                            background: url("/assets/images/locate1.jpeg");
                            width: 100%;
                            height: 224px;
                            background-size: cover;
                            background-repeat: no-repeat;
                            background-position: center center;
                            @include media-max(sm){
                                height: 160px;
                                margin-right: 15px;
                            }
                            @include media-max(xs){
                                margin-right: 0px;
                            }
                        }
                        .card-body{
                            width: 100%;
                            padding: 0; 
                            border: 1px solid map-get($card-group,'border-color');                          
                        }
                        @include media-max(sm){
                            max-width: 100%;
                            flex-direction: row;
                            justify-content: space-around;
                            align-items: center;
                            border: 0;
                            img{
                                max-width: 330px;
                            }
                        }
                    }
                    iframe{
                        width: 635px;
                        @include media-max(md){
                            width: 535px;
                        }
                        @include media-max(sm){
                            width: 100%;
                        }
                    }
                }
            }
        }
        .section-qa{
            h3{
                margin-bottom: 36px !important;
                @include media-max(md){
                    margin-bottom: 32px !important;
                }
                @include media-max(xs){
                    margin-bottom: 12px !important;
                }
            }
            ul{
                li{
                    margin-bottom: 40px;
                    h5{
                        font-size: 20px;
                        line-height: 23px;
                        margin-bottom: 16px;
                        @include media-max(xs){
                            margin-bottom: 8px;
                        }
                    }
                    p{
                        font-size: 16px;
                        line-height: 24px;
                        margin-left: 90px;
                        @include media-max(xs){
                            margin-left: 0px;
                            margin-bottom: 0px;
                        }
                        &:first-child{
                            margin-bottom: 15px !important;
                        }
                    }
                }
            }
        }
        .section-news{
            .item-news{
                display: flex;
                margin-bottom: 60px;
                @include media-max(sm){
                    margin-bottom: 32px;
                }
                @include media-max(xs){
                    display: block;
                }
            }
            .item-pic{
                width: 50%;
                margin-right: 30px;
                background: url("/assets/images/blog-1.png");
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center center;
                @include media-max(xs){
                    width: 100%;
                    height: 193px;
                }
            }
            .item-content{
                width: 50%;
                @include media-max(xs){
                    width: 100%;
                }
                h3{
                    color: #650300;
                    font-size: 32px;
                    line-height: 63px;
                    font-weight: bold;
                    margin-bottom: 0px;
                    @include media-max(sm){
                        font-size: 20px;
                        line-height: 30px;
                    }
                }
                .item-sub-title{
                    color:#000;
                    font-size: 20px;
                    line-height: 30px;
                    @include media-max(sm){
                        font-size: 16px;
                        line-height: 30px;
                    }
                }
                .item-date{
                    color: rgba(0,0,0,0.7);
                    font-size: 16px;
                    line-height: 24px;
                    @include media-max(sm){
                        font-size: 14px;
                        line-height: 24px;
                    }
                }
                p{
                    color:#555555;
                    font-size: 20px;
                    line-height: 30px;
                    margin-bottom: 19px;
                    @include media-max(sm){
                        font-size: 14px;
                        line-height: 21px;
                    }
                    @include media-max(xs){
                        display: none;
                    }
                }
                .btn-more {
                    color: #650300;
                    font-size: 34px;
                    line-height: 54px;
                    font-weight: bold;
                    text-decoration: none;
                }
            }
        }
        .section-news-detail{
            padding: 8px 0 304px 0; 
            .news-router{
                margin-bottom: 17px;
                @include media-max(sm){
                    margin-bottom: 8px;
                }
                a{
                    color: #000;
                    &.now{
                        color: #AA0601;
                    }
                }
            }
            .news-tag{
                a{
                    font-size: 24px;
                    line-height: 36px;
                    color: #AA0601;
                    padding: 0px 8px 0 0;
                    font-weight: bold;
                }
            }
            .news-content{
                display: flex;
                @include media-max(xs){
                    display: block;
                }
            }
            .news-tag-group{
                width: 50%;
                @include media-max(xs){
                    width: 100%;
                }
                img{
                    width: 48px;
                    @include media-max(sm){
                        width: 24px;
                    }
                }
                span{
                    font-size: 28px;
                    line-height: 42px;
                    color: #AA0601;
                    margin-left: 8px;
                    font-weight: bold;
                    @include media-max(sm){
                        font-size: 20px;
                        line-height: 42px;
                    }
                }
                a{
                    padding: 0;
                    font-size: 24px;
                    line-height: 36px;
                    color: #AA0601;
                    font-weight: bold;
                    margin-right: 24px;
                    margin-bottom: 8px;
                    @include media-max(sm){
                        font-size: 16px;
                        line-height: 21px;
                        margin-bottom: 0px;
                    }
                    @include media-max(sm){
                        margin-right: 8px;
                    }
                }
            }
            .news-article{
                width: 100%;
                max-width: 825px;
                margin-right: 30px;
                h1{
                    margin: 0;
                    padding: 0;
                    color: #000 !important;
                }
                .item-sub-title{
                    font-size: 28px;
                    line-height: 42px;
                    @include media-max(sm){
                        font-size: 16px;
                        line-height: 21px;
                    }
                }
                .item-date{
                    font-size: 16px;
                    line-height: 24px;
                    color: rgba(0,0,0,0.7);
                    @include media-max(sm){
                        font-size: 14px;
                    }
                }
                img{
                    margin-bottom: 24px !important;
                    @include media-max(sm){
                        margin-bottom: 16px !important;
                    }
                }
                p{
                    margin-bottom: 36px;
                    color: #0A0A0A;
                    opacity: 0.6;
                    font-size: 20px;
                    line-height: 30px;
                    font-weight: bold;
                }
            }
            .news-socail-share{
                display: flex;
                span{
                    font-size: 20px;
                    line-height: 30px;
                    font-weight: bold;
                    padding-right: 20px;
                }
                img{
                    padding-right: 16px;
                }
                padding-bottom: 16px;
                border-bottom: 1px solid #707070;
            }
            .news-way{
                display: flex;
                justify-content: space-between;
                padding-top: 16px;
                margin-bottom: 40px;
                .go{
                    font-size: 19px;
                    line-height: 30px;
                }
                @include media-max(xs){
                    display: block;
                }
            }
        }
    }
}

.tag{
    width: 24px;
    height: 24px;
    border-radius: 4px;
    &.__yellow{
        background: map-get($inner-group,'product-tag-1');
    }
    &.__brown{
        background: map-get($inner-group,'product-tag-2');
    }
}