footer{
  background:map-get($footer-group,'background-color');
  padding: 48px 0 28px 0;
  @include media-max(md){
    padding: 40px;
  }
  @include media-max(sm){
    padding: 24px 15px;
  }
  @include media-max(xs){}
  a{
    color:map-get($footer-group,'text-normal-color');
    text-decoration: none;
    @include media-max(sm){
      font-size: 16px;
      line-height: 24px;
    }
  }
  .footer-top-box{
    display: flex;
    justify-content: space-between;
    ul{
      display: flex;
      align-items: center;
      justify-content:space-between;
      font-size: 16px;
      line-height: 24px;
      &:first-child{
        width: 464px;
        @include media-max(md){
          width: 432px;
        }
        @include media-max(sm){
          width: 392px;
        }
        @include media-max(xs){
          display: none;
        }
      }
      &:last-child{
        width: 208px;
        @include media-max(md){
          width: 120px;
          img{
            width: 24px;
          }
        }
        @include media-max(xs){
          display: none;
        }
      }
    }
  }
  .footer-middle-box{
    border-bottom: 1px solid white;
    display: flex;
    justify-content: space-between;
    ul{
      &:last-child{
        display: flex;
        justify-content:space-between;
        font-size: 16px;
        line-height: 24px;
        width: 208px;
        display:none;
        @include media-max(xs){
          display: flex;
          width: 120px;
          img{
            width: 24px;
            height: 24px;
          }
        }
      }
      &:first-child{
        a{
          font-size: 36px;
          line-height: 54px;
          @include media-max(md){
            font-size: 20px;
            line-height: 30px;
          }
          img{
            width: 44px;
            height: 44px;
          }
        }
      }
    }
  }
  .footer-bottom-box{
    display: flex;
    justify-content:space-between;
    position: relative;
    @include media-max(xs){
      display: block;
    }
    ul{
      display: flex;
      align-items: center;
      @include media-max(xs){
        display: block;
        margin-bottom: 8px !important;
      }
      &.footer-info{
        font-size: 16px;
        line-height: 24px;
        color: white;
        img{
          width: 80px;
          margin-right: 38px;
          @include media-max(md){
            display: none;
          }
        }
      }
      &.footer-privacy{
        li{
          padding-right: 30px;
          @include media-max(xs){
            padding-bottom: 8px;
          }
          &:last-child{
            padding-right: 0px;
          }
          a{
            display: block;
            font-size: 14px;
            line-height: 21px;
          }
        }
      }
    }
    a.footer-bottom-logo{
      display: block;
      img{
        max-width: 46px;
        display: none;
        @include media-max(xs){
          display: block;
        }
      }
      @include absolute(bottom 5px right 0);
    }
  }
}